.players {
  width: calc(100% - 50px); }
  @media (min-width: 768px) {
    .players {
      width: 100%; } }
  .players .barchart ul li i {
    line-height: 15px !important; }
    .players .barchart ul li i span.single {
      line-height: 30px !important; }
    @media (min-width: 768px) {
      .players .barchart ul li i {
        line-height: 20px !important; }
        .players .barchart ul li i span.single {
          line-height: 40px !important; } }



