.teams {
  width: calc(100% - 50px); }
  @media (min-width: 768px) {
    .teams {
      width: 100%; } }
  .teams .barchart ul li i {
    line-height: 15px !important; }
    .teams .barchart ul li i span.single {
      line-height: 30px !important; }
    @media (min-width: 768px) {
      .teams .barchart ul li i {
        line-height: 20px !important; }
        .teams .barchart ul li i span.single {
          line-height: 40px !important; } }



