.regression-line {
  opacity: 0; }
  .regression-line path {
    fill: none;
    stroke: #000;
    stroke-width: 4;
    stroke-dasharray: 6 12;
    stroke-linecap: square; }
    .regression-line path.bg {
      stroke: #fff;
      stroke-width: 8;
      stroke-dasharray: 6 12; }
  .regression-line text {
    text-transform: uppercase;
    text-anchor: start;
    font-size: 16px;
    font-weight: bold;
    stroke: #fff;
    stroke-width: 3px;
    paint-order: stroke; }
  .intro .regression-line {
    opacity: 0.8; }
  .preface .regression-line {
    opacity: 0; }



