:root {
  /* #f0f0f0 in decimal RGB */
  --barcolor: 243,213,136; }

.barchart {
  font-family: 'Roboto', sans-serif; }
  .barchart ul {
    margin: 0;
    padding: 0;
    list-style: none; }
    .barchart ul li {
      position: relative;
      height: 30px;
      line-height: 30px;
      font-size: 12px;
      margin-bottom: 6px;
      display: -ms-flexbox;
      display: flex; }
      @media (min-width: 768px) {
        .barchart ul li {
          height: 40px;
          font-size: 15px;
          line-height: 40px; } }
      .barchart ul li i {
        font-style: normal;
        width: 250px;
        display: inline-block;
        text-align: right;
        white-space: nowrap;
        overflow: hidden; }
      .barchart ul li div.bars {
        position: relative;
        width: calc(100% - 10px);
        margin-left: 10px; }
        .barchart ul li div.bars span {
          position: absolute;
          left: 0;
          top: 0;
          height: 100%;
          background-color: rgba(var(--barcolor), 0.75);
          opacity: 0.75;
